import React from 'react';
import MobileHeader from './mobile-header';
import MobileFooter from './mobile-footer';
import MobileList from './mobile-list';
import {
	Menu,
	Icon,
	Tabs,
	message,
	Form,
	Input,
	Button,
	CheckBox,
    Modal,
    Carousel
} from 'antd';
const TabPane = Tabs.TabPane;
export default class MobileIndex extends React.Component{
    render(){
        const settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            autoplay: true
        }
        return(
            <div>
                <MobileHeader></MobileHeader>
                <Tabs>
                    <TabPane tab="" key="1">
                        <div class="carousel">
                            <Carousel {...settings}>
                                <div><img src="./src/images/carousel_1.jpg"/></div>
                                <div><img src="./src/images/carousel_2.jpg"/></div>
                                <div><img src="./src/images/carousel_3.jpg"/></div>
                                <div><img src="./src/images/carousel_4.jpg"/></div>
                            </Carousel>
                        </div>
                        <MobileList count={16} type="top"></MobileList>
                    </TabPane>
                    <TabPane tab="" key="2">
                        <MobileList count={16} type="guonei"></MobileList>
                    </TabPane>
                    <TabPane tab="" key="3">
                        <MobileList count={16} type="guoji"></MobileList>
                    </TabPane>
                    <TabPane tab="" key="4">
                        <MobileList count={16} type="yule"></MobileList>
                    </TabPane>
                    <TabPane tab="" key="5">
                        <MobileList count={16} type="tiyu"></MobileList>
                    </TabPane>
                </Tabs>
                <MobileFooter></MobileFooter>
            </div>
        );
    }
}


