import React from 'react';
import {Row, Col, Card, Form, Input, Button, notification} from 'antd';
import {
    BrowserRouter as Router,
    Route,
    Link
  } from 'react-router-dom';
const FormItem = Form.Item;
class CommonComments extends React.Component{
    constructor(){
        super();
        this.state = {
            comments:''
        };
    };
    componentDidMount() {
        var myFetchOptions = {
            method: 'GET'
        };
        fetch("http://newsapi.gugujiankong.com/Handler.ashx?action=getcomments&uniquekey="+this.props.uniquekey, myFetchOptions).then(response => response.json()).then(json => {
            this.setState({comments: json});
        });
    };
    handleSubmit(e){
        e.preventDefault();
        var myFetchOptions = {
            method: 'GET'
        };
        var formData = this.props.form.getFieldsValue();
        fetch("http://newsapi.gugujiankong.com/Handler.ashx?action=comment&userid=" + localStorage.userid + "&uniquekey=" + this.props.uniquekey + "&commnet=" + formData.remark, myFetchOptions).then(response => response.json()).then(json => {
			this.componentDidMount();
		});
        
        
    };
    addUserCollection(){
        var myFetchOptions = {
            method: 'GET'
        };
        fetch("http://newsapi.gugujiankong.com/Handler.ashx?action=uc&userid="+localStorage.userid+"&uniquekey="+this.props.uniquekey,myFetchOptions)
        .then(response=>response.json())
        .then(json=>{
            //
            notification['success']({message:"reactNews", description:''});
        });
    }
    render(){
        const {getFieldDecorator} = this.props.form;
        const {comments} = this.state;
        const commentList = comments.length
                            ?
                            comments.map((comment,index)=>(
                                <Card key={index} title={comment.UserName} extra={<a href="#">{comment.datetime}</a>}>
                                    <p>{comment.Comments}</p>
                                </Card>
                            )):
                            '';

        return (
            <div class="comments">
                <Row>
                    <Col span={24}>
                        {commentList}
                        <Form onSubmit={this.handleSubmit.bind(this)}>
                            <FormItem label="">
                                {getFieldDecorator('remark', {
                                    initialValue:''
                                })(
                                    <Input type="textarea" placeholder=""/>
                                )}
                                
                            </FormItem>
                            <Button type="primary" htmlType="submit"></Button>&nbsp;&nbsp;
                            <Button type="primary" htmlType="button" onClick={this.addUserCollection.bind(this)}></Button>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    };
    
}
export default CommonComments = Form.create({})(CommonComments);


